.header {
  display: flex;
  height: 4rem;
  padding: 0.8em 1em;
  background: black;
  color: white;
}

.logo {
  display: flex;
  align-items: center;
  margin-right: 1em;
}

.img {
  margin-right: 0.2em;
}

.input {
  flex-basis: 100%;
  font-size: 1.2rem;
  outline: 0;
}

.button {
  background-color: darkgrey;
  outline: 0;
  border: none;
}

.buttonImg {
  padding: 0.5em 0.2em;
  height: 100%;
}

@media screen and (max-width: 48rem) {
  .header {
    font-size: 0.7rem;
  }
}
